<template>
  <div style="width:100%">
    <div v-if="founded" class="outline-wrap">
      <div class="head">{{outlineName}}</div>
      <el-form class="create-form" label-width="90px">
        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="大纲学期：">{{outlineSeason}}</el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="课程名称：">{{lessonName}}</el-form-item>
          </el-col>
        </el-row>
        <el-form-item label="教学目标：">
          <p v-html="teachTarget"></p>
        </el-form-item>
        <el-form-item label="本课程教学方式：">
          <p v-html="teachWays"></p>
        </el-form-item>
        <el-form-item label="教学安排及方式：">
          <p v-html="teachArrange"></p>
        </el-form-item>
        <el-form-item label="考核方式：">
          <p v-html="teachCheck"></p>
        </el-form-item>
        <el-form-item label="推荐教材与参考：">
          <p v-html="recommand"></p>
        </el-form-item>
      </el-form>
    </div>
    <!-- 没有数据 -->
    <div v-if="!founded" class="not-found-wrap">
      <div class="img-box">
        <img src="../../../../assets/not_outline.png" :draggable="false">
      </div>
      <div class="text">{{text}}</div>
      <div class="tip">{{tip}}</div>
    </div>
  </div>
</template>
<script>
import http from "../../../../utils/http";
import api from "../../../../utils/api";

export default {
  props: ["founded", "tpidFirst"],
  data() {
    return {
      //不可修改↓
      text: "",
      tip: "",
      oText: "您还没有创建大纲",
      oTip: "立刻点击创建按钮，开始创建大纲吧~ ！",
      cText: "您还没有创建目录",
      cTip: "右键点击左侧栏的大纲，创建新的目录吧~ ！",
      //不可修改↑
      tpid: "",
      outlineName: "",
      outlineSeason: "",
      lessonName: "",
      teachTarget: "",
      teachWays: "",
      teachArrange: "",
      teachCheck: "",
      recommand: ""
    };
  },
  mounted() {
    if (this.$route.query.c) {
      this.text = this.cText;
      this.tip = this.cTip;
    } else {
      this.text = this.oText;
      this.tip = this.oTip;
    }

    if (this.$route.query.tpid) {
      this.tpid = this.$route.query.tpid;
    } else {
      this.tpid = this.tpidFirst;
    }

    this.getData();
  },
  methods: {
    getData: async function() {
      const res = await http.get(api.getTeachingProgramData, {
        id: this.tpid,
        Token: http.getToken()
      });
      if (res.Result) {
        if (res.Data == null) return;

        this.outlineName = res.Data.TPName;
        this.outlineSeason = res.Data.TermName;
        this.lessonName = res.Data.CourseName;
        this.teachTarget = res.Data.TPgoal;
        this.teachWays = res.Data.TPMethods;
        this.teachArrange = res.Data.TPArrange;
        this.teachCheck = res.Data.TPAssessment;
        this.recommand = res.Data.TPRecommend;
      }
    }
  },
  watch: {
    $route: {
      handler: function(to, from) {
        this.tpid = to.query.tpid;
        this.getData();
      },
      deep: true
    },
    tpidFirst: {
      handler: function(val, oldVal) {
        this.tpid = val;
        this.getData();
      },
      deep: true
    }
  }
};
</script>
<style lang="less" scoped>
.not-found-wrap {
  width: 100%;
  height: 666px;
  background: #fff;
  padding-top: 66px;
  user-select: none;
  .img-box {
    width: 66%;
    margin: 0 auto;
    img {
      width: 100%;
    }
  }
  .text {
    margin-top: 25px;
    color: #000;
    font-size: 1.52rem;
  }
  .tip {
    margin-top: 8px;
    color: #aaa;
    font-size: 1.1rem;
  }
}
</style>


